.working {
  @media (max-width: 870px) {
    align-self: center;
  }

  @media (max-width: 610px) {
    display: none;
  }
}

.working__caption {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #333333;
  margin-top: 0;
  margin-bottom: 3px;

  @media (max-width: 650px) {
    font-size: 12px;
    line-height: 16px;
  }
}

.working__text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #828282;

  @media (max-width: 650px) {
    font-size: 11px;
    line-height: 14px;
  }
}
